import { useDispatch, useSelector } from "react-redux";
import {
  setDroppedObjectData,
  setParentDetailsLoading,
} from "../store/droppedObjectSlice";
import useToast from "../hooks/useToast";

import api from "../utils/api"; // Import axios instance
import { MSG_REPLACE_SUCCESS, MSG_REPLACE_ERROR } from "../utils/toastMessages";

const useRevisions = () => {
  const dispatch = useDispatch();
  const { showSuccessToast, showErrorToast } = useToast();

   // Get the current parent details from Redux
   const currentParentDetails = useSelector(
    (state) => state.droppedObject.droppedObjectData.parentDetails
  );

  const fetchRevisionsAndParents = async (
    objectId,
    objectType,
    relativePath
  ) => {
    if (!relativePath) {
      console.error("[Fetch Revisions] Missing relative path.");
      return;
    }

    const parentURL = `/revFloat/getParents`; // Use relative path for axios

    try {
      dispatch(setParentDetailsLoading(true));
      // console.log(
      //   "[fetchRevisionsAndParents] Setting loadingParentDetails to true"
      // );
      // console.log("objectId", objectId);
      // console.log("type", objectType);
      // Use the axios instance to make the POST request
      const response = await api.post(parentURL, {
        data: {
          id: objectId,
          type: objectType,
          relativePath: relativePath,
        },
      });

      if (response.status === 200) {
        const parentDetails = response.data;
        // console.log("[Fetch Parent Information] Parents fetched:", parentDetails);
        const isSameData =
        JSON.stringify(currentParentDetails) === JSON.stringify(parentDetails);

        if (!isSameData) {
          dispatch(
            setDroppedObjectData({
              parentDetails: parentDetails,
            })
          );
        } else {
          console.log("[Fetch Revisions] Data unchanged. Skipping Redux update.");
        }
      } else {
        throw new Error(
          `[Object Details] HTTP error! status: ${response.status}`
        );
      }
    } catch (error) {
      console.error("[Fetch Revisions] Error occurred:", error);
      dispatch(setParentDetailsLoading(false));
    } finally {
      dispatch(setParentDetailsLoading(false));
      // console.log(
      //   "[fetchRevisionsAndParents] Setting loadingParentDetails to false"
      // );
    }
  };

 

  return { fetchRevisionsAndParents,  };
};

export default useRevisions;
